import React from "react";
// import { Form } from "../Formik/FormOfLevels";
import * as Yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import { blue, pink } from "@material-ui/core/colors";
import Container from "@material-ui/core/Container";
import { Field, Form, Formik, FormikProps } from "formik";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import TextField from "@material-ui/core/TextField";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "100%",
      height: "10ch",
    },
  },
  color: {
    backgroundColor: blue[600],
    color: theme.palette.getContrastText(pink[500]),
  },
}));

interface Props {
  onSubmit: (data: any) => void;
  level: any;
  name: string;
  onClick?: () => void;
}

const InputField = ({ field, form, ...props }: any) => {
  return <TextField {...props} {...field} />;
};

const LevelForm: React.FC<Props> = ({ onSubmit, level, name, onClick }) => {
  const validationSchema: any = Yup.object().shape({
    levelname: Yup.string().min(5).max(20).required("Level Name is Required"),
  });

  const addValues = {
    levelname: undefined,
  };

  const editValues = level && {
    levelname: level.levelname,
  };
  const values = name === "edit" ? editValues : addValues;
  const classes = useStyles();
  return (
    <Container>
      <Formik
        onSubmit={(data, { resetForm }) => {
          onSubmit({ row: data, id: level.levelId, levelData: level });
          if (name !== "edit") resetForm();
        }}
        initialValues={values}
        validationSchema={validationSchema}
      >
        {({ errors, touched, isValid }: FormikProps<any>) => (
          <Form className={classes.root}>
            <Box display="flex" p={1}>
              <Box p={1} flexGrow={1}>
                <div>
                  <Field
                    variant="outlined"
                    name="levelname"
                    label="Level Name"
                    helperText={
                      errors.levelname && touched.levelname
                        ? errors.levelname
                        : null
                    }
                    error={touched.levelname && Boolean(errors.levelname)}
                    component={InputField}
                  />
                </div>
              </Box>
              <Box p={1}>
                <Button
                  variant="contained"
                  onClick={onClick}
                  className={classes.color}
                  type="submit"
                  disabled={!isValid}
                >
                  Submit
                </Button>
              </Box>
            </Box>
          </Form>
        )}
      </Formik>
    </Container>
  );
};

export default LevelForm;
