import http from "./httpServices";

const URL = "/grades";

export const getGrades = (page = 0, size = 5) => {
	const grades = http.get(`${URL}/?page=${page}&size=${size}`);
	return grades;
};

export const postGrade = (data: any) => {
	const grade = http.post(URL, data);
	return grade;
};

export const deleteGrade = (id: any) => {
	const grade = http.delete(`${URL}/${id}`);
	return grade;
};
export const updateGrade = (data: any) => {
	const grade = http.put(`${URL}/${data.id}`, data.row);
	return grade;
};
