import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import { LevelGraph } from "../Graphs";
import Typography from "@material-ui/core/Typography";
import Cards from "./Cards";
import Progress from "../Loading/progress";
import { getTeachers } from "../../services/teacherServices";
import { getSubjects } from "../../services/subjectService";
import { getStudents } from "../../services/studentServices";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    width: "100%",
    padding: theme.spacing(1),
    textAlign: "left",
    color: theme.palette.text.secondary,
  },
}));

const Home = () => {
  const classes = useStyles();
  const [studentItems, setStudentItems] = useState(0);
  const [subjectItems, setSubjects] = useState(0);
  const [teacherItems, setTeachers] = useState(0);
  const [students, setStudents] = useState<Array<{}>>([]);
  const [isLoading, setLoading] = useState(true);
  useEffect(() => {
    const fetchSubjects = async () => {
      const { data } = await getSubjects();
      const { totalItems } = data;
      setSubjects(totalItems);
      setLoading(false);
    };
    const fetchStudents = async (page: number, size: number) => {
      const { data } = await getStudents(page, size);
      const { totalItems, students } = data;
      setStudentItems(totalItems);
      setStudents(students);
      setLoading(false);
    };
    const fetchTeachers = async () => {
      const { data } = await getTeachers();
      const { totalItems } = data;
      setTeachers(totalItems);
      setLoading(false);
    };
    fetchSubjects();
    fetchStudents(0, studentItems);
    fetchTeachers();
  }, [studentItems]);
  return (
    <>
      {isLoading ? (
        <Progress />
      ) : (
        <div className={classes.root}>
          <Cards
            subjectItems={subjectItems}
            studentItems={studentItems}
            teacherItems={teacherItems}
          />
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Paper className={classes.paper}>
                <Typography
                  variant="h6"
                  align="left"
                  style={{ marginBottom: 20 }}
                  gutterBottom
                >
                  Level/Graph
                </Typography>
                <LevelGraph students={students} />
              </Paper>
            </Grid>
            <Grid item xs={3}></Grid>
          </Grid>
        </div>
      )}
    </>
  );
};

export default Home;
