import http from "./httpServices";

const URL = "/teachers";

export const getTeachers = (page = 0, size = 10) => {
	const teachers = http.get(`${URL}/?page=${page}&size=${size}`);
	return teachers;
};

export const postTeachers = (data: any) => {
	const teachers = http.post(URL, data);
	return teachers;
};

export const deleteTeacher = (id: number) => {
	const teacher = http.delete(`${URL}/${id}`);
	return teacher;
};

export const updateTeacher = (data: any) => {
	const teacher = http.put(`${URL}/${data.id}`, data.row);
	return teacher;
};
