import Box from "@material-ui/core/Box";
import { blue, pink } from "@material-ui/core/colors";
import Container from "@material-ui/core/Container";
import Paper from "@material-ui/core/Paper";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Pagination from "@material-ui/lab/Pagination";
import React, { Fragment, useState, useEffect } from "react";
import Grid from "@material-ui/core/Grid";
import {
  getLevels,
  postLevels,
  deleteLevel,
  updateLevel,
} from "../../services/levelServices";
import LevelForm from "../Forms/levelForm";
import Progress from "../Loading/progress";
import LevelPopForm from "../Forms/PopUpForms/levelPop";
import DeletePopUp from "../Forms/PopUpForms/deletePop";
import SnackPar from "../Common/snackpar";
import Search from "../Search/search";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "",
  },
  table: {
    minWidth: 100,
  },
  gridRoot: {
    flexGrow: 1,
  },

  paper: {
    padding: theme.spacing(2),
    textAlign: "left",
    color: theme.palette.text.secondary,
  },
  button: {
    cursor: `pointer`,
  },
  head: {
    backgroundColor: `#f5f5f5`,
    color: theme.palette.common.white,
    fontStyle: "fontWeightBold",
  },
  pagination: {
    margin: theme.spacing(2),
    marginTop: theme.spacing(5),
  },

  fab: {
    margin: theme.spacing(2),
    color: theme.palette.getContrastText(pink[500]),
    backgroundColor: blue[600],
  },
}));

const isSearched = (value: string) => ({ levelname }: any) =>
  levelname.toLowerCase().includes(value.toLowerCase());

export function LevelTable() {
  const classes = useStyles();
  const [levels, setLevels] = useState<Array<{}>>([]);
  const [isLoading, setLoading] = useState(true);
  const [errors, setErrors] = useState(null);
  const [value, setValue] = useState<string>("");
  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState("");
  const [totalPages, setTotalPages] = useState(0);
  const [page, setPage] = useState(1);
  const [totalItems, setTotalItems] = useState(0);
  const [size, setSize] = useState(5);

  useEffect(() => {
    const fetchLevels = async (page: number, size: number) => {
      const { data } = await getLevels(page, size);
      setTotalItems(data.totalItems);
      setTotalPages(data.totalPages);
      setLevels(data.levels);
      setLoading(false);
    };
    fetchLevels(page - 1, size);
  }, [page, size]);

  const handleSubmit = async ({ row }: any) => {
    try {
      const { data } = await postLevels(row);
      const newLevels = [...levels, data];
      setLevels(newLevels);
      handleClick("Sucessfully posted!");
    } catch (err) {
      handleClick("err!");
      setErrors(err.message);
    }
  };

  const handleDelete = async (id: number) => {
    try {
      const cloneLevels = [...levels];
      const newLevels = cloneLevels.filter(
        (level: any) => level.levelId !== id
      );
      setLevels(newLevels);
      await deleteLevel(id);
      handleClick("Sucessfully deleted!");
    } catch (err) {
      handleClick("err!");
      setErrors(err.message);
      const cloneLevels = [...levels];
      setLevels(cloneLevels);
    }
  };
  const fetchPaginatedData = (e: any, page: number) => {
    setPage(page);
  };
  const handleUpdate = async ({ row, id, levelData }: any) => {
    try {
      const { data } = await updateLevel({ row, id });
      const cloneLevels = [...levels];
      const index = cloneLevels.indexOf(levelData);
      cloneLevels[index] = { ...data };
      setLevels(cloneLevels);
      handleClick("Sucessfully updated!");
    } catch (err) {
      handleClick("err!");
      setErrors(err.message);
    }
  };

  const handleClick = (msg: string) => {
    setMessage(msg);
    setOpen(true);
  };

  const handleClose = (event: any, reason: any) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
  };
  const handleChange = (e: any) => {
    if (e.target.value) {
      setValue(e.target.value);
      setSize(totalItems);
    } else {
      setValue(e.target.value);
      setSize(5);
    }
  };

  // function Alert(props: any) {
  //   return <MuiAlert elevation={6} variant="filled" {...props} />;
  // }

  // const handleClick = () => {
  //   setOpen(true);
  // };

  // const handleClose = (event: any, reason: any) => {
  //   if (reason === "clickaway") {
  //     return;
  //   }

  //   setOpen(false);
  // };

  return (
    <Fragment>
      <SnackPar
        errors={errors}
        handleClose={handleClose}
        success={message}
        open={open}
      />
      <Container style={{ width: "100%", marginBottom: 20 }}>
        <Box display="flex" p={1}>
          <Box p={1} flexGrow={1}>
            <Search value={value} onChange={handleChange} by="Level Name" />
          </Box>
        </Box>
      </Container>

      <div className={classes.gridRoot}>
        <Grid container spacing={3}>
          <Grid item xs={5}>
            {isLoading ? (
              <Progress />
            ) : (
              <TableContainer component={Paper} className={classes.root}>
                <Table className={classes.table} aria-label="simple table">
                  <TableHead className={classes.head}>
                    <TableRow>
                      <TableCell>#ID</TableCell>
                      <TableCell>Level Name</TableCell>
                      <TableCell align="center">Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  {levels && (
                    <TableBody>
                      {levels.filter(isSearched(value)).map((level: any) => (
                        <TableRow key={level.levelId}>
                          <TableCell component="th" scope="row">
                            {level.levelId}
                          </TableCell>
                          <TableCell>{level.levelname}</TableCell>
                          <TableCell style={{ display: "flex" }}>
                            <LevelPopForm
                              onSubmit={handleUpdate}
                              level={level}
                            />

                            <DeletePopUp
                              item={level.levelId}
                              onDelete={handleDelete}
                            />
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  )}
                </Table>
              </TableContainer>
            )}
            {totalPages !== 0 ? (
              <Pagination
                className={classes.pagination}
                count={totalPages}
                page={page}
                onChange={fetchPaginatedData}
                color="primary"
              />
            ) : null}
          </Grid>
          <Grid item xs={7}>
            <Paper elevation={0} variant="outlined" className={classes.paper}>
              <h3>Level Form</h3>
              <LevelForm level="level" name="add" onSubmit={handleSubmit} />
            </Paper>
          </Grid>
        </Grid>
      </div>
    </Fragment>
  );
}
