import React from "react";
import { Field, Form, Formik, FormikProps } from "formik";
import * as Yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Fab from "@material-ui/core/Fab";
import Tooltip from "@material-ui/core/Tooltip";
import AddIcon from "@material-ui/icons/Add";
import { blue, pink } from "@material-ui/core/colors";
import EditIcon from "@material-ui/icons/Edit";
import {
  FormControl,
  FormControlLabel,
  FormLabel,
  IconButton,
  Radio,
  RadioGroup,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "35ch",
      height: "10ch",
    },
  },
  formControl: {
    marginRight: theme.spacing(1),
    minWidth: 200,
  },
  blueLight: {
    color: blue[600],
  },
  selectEmpty: {
    marginTop: theme.spacing(1),
  },
  fab: {
    margin: theme.spacing(2),
    backgroundColor: blue[600],
    color: theme.palette.getContrastText(pink[500]),
  },
}));

interface Props {
  onSubmit: (data: any) => void;
  name: string;
  teacher: any;
}

const TeacherForm: React.FC<Props> = ({ onSubmit, name, teacher }) => {
  const validationSchema: any = Yup.object({
    firstname: Yup.string().required("firstname is required"),
    secondname: Yup.string().required("secondname is required"),
    lastname: Yup.string().required("lastname is required"),
    contact: Yup.number().required("contact is required"),
    gender: Yup.string().required("gender is required"),
  });

  const genderSelect = ({ field, form, ...props }: any) => {
    return (
      <FormControl component="fieldset">
        <FormLabel component="legend">Gender</FormLabel>
        <RadioGroup {...props} {...field}>
          {["male", "female"].map((option) => (
            <div>
              <FormControlLabel
                value={option}
                control={<Radio />}
                label={option}
              />
            </div>
          ))}
        </RadioGroup>
      </FormControl>
    );
  };
  const InputField = ({ field, form, ...props }: any) => {
    return <TextField {...props} {...field} />;
  };
  const addValues = {
    firstname: undefined,
    secondname: undefined,
    lastname: undefined,
    contact: undefined,
    gender: undefined,
  };

  const editValues = {
    firstname: teacher.firstname,
    secondname: teacher.secondname,
    lastname: teacher.lastname,
    contact: teacher.contact,
    gender: teacher.gender,
  };
  const values = name === "edit" ? editValues : addValues;

  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const classes = useStyles();
  return (
    <div>
      <div>
        {name === "edit" ? (
          <Tooltip title="Edit">
            <IconButton onClick={handleClickOpen} aria-label="edit">
              <EditIcon className={classes.blueLight} />
            </IconButton>
          </Tooltip>
        ) : (
          <Tooltip title="Add" aria-label="add">
            <Fab
              className={classes.fab}
              color="secondary"
              onClick={handleClickOpen}
            >
              <AddIcon />
            </Fab>
          </Tooltip>
        )}
        <Dialog
          maxWidth="md"
          open={open}
          onClose={handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Teacher Form</DialogTitle>
          <DialogContent>
            <DialogContentText>Fill Teacher Information</DialogContentText>
            <Formik
              onSubmit={(data, { resetForm }) => {
                onSubmit({
                  row: data,
                  id: teacher.teacherId,
                  dataIndex: teacher,
                });
                if (name !== "edit") resetForm();
              }}
              initialValues={values}
              validationSchema={validationSchema}
            >
              {({ errors, touched, isValid }: FormikProps<any>) => (
                <Form className={classes.root}>
                  <Field
                    variant="outlined"
                    name="firstname"
                    label="First Name"
                    helperText={
                      errors.firstname && touched.firstname
                        ? errors.firstname
                        : null
                    }
                    error={touched.firstname && Boolean(errors.firstname)}
                    component={InputField}
                  />

                  <Field
                    variant="outlined"
                    name="secondname"
                    label="Second Name"
                    helperText={
                      errors.secondname && touched.secondname
                        ? errors.secondname
                        : null
                    }
                    error={touched.secondname && Boolean(errors.secondname)}
                    component={InputField}
                  />
                  <Field
                    variant="outlined"
                    name="lastname"
                    label="Last Name"
                    helperText={
                      errors.lastname && touched.lastname
                        ? errors.lastname
                        : null
                    }
                    error={touched.lastname && Boolean(errors.lastname)}
                    component={InputField}
                  />
                  <Field
                    variant="outlined"
                    name="contact"
                    label="Contact"
                    helperText={
                      errors.contact && touched.contact ? errors.contact : null
                    }
                    error={touched.contact && Boolean(errors.contact)}
                    component={InputField}
                  />
                  <Field
                    labelId="gender"
                    name="gender"
                    id="gender"
                    label="Gender"
                    component={genderSelect}
                  />

                  <DialogActions>
                    <Button onClick={handleClose} color="primary">
                      Cancel
                    </Button>
                    <Button
                      type="submit"
                      disabled={!isValid}
                      onClick={handleClose}
                      color="primary"
                    >
                      Submit
                    </Button>
                  </DialogActions>
                </Form>
              )}
            </Formik>
          </DialogContent>
        </Dialog>
      </div>
    </div>
  );
};

export default TeacherForm;
