import httt from "./httpServices";

const URL = "/subjects";

export const getSubjects = (page = 0, size = 5) => {
	const subjects = httt.get(`${URL}/?page=${page}&size=${size}`);
	return subjects;
};

export const postSubjects = (data: any) => {
	const subject = httt.post(URL, data);
	return subject;
};

export const updatesubject = (data: any) => {
	const subject = httt.put(`${URL}/${data.id}`, data.row);
	return subject;
};

export const deletesubject = (id: number) => {
	const subject = httt.delete(`${URL}/${id}`);
	return subject;
};
