import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Field, Form, Formik, FormikProps } from "formik";
import Container from "@material-ui/core/Container";
import { blue, pink } from "@material-ui/core/colors";
import * as Yup from "yup";
import TextField from "@material-ui/core/TextField";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "100%",
      height: "10ch",
    },
  },
  color: {
    backgroundColor: blue[600],
    color: theme.palette.getContrastText(pink[500]),
  },
}));

interface Props {
  onSubmit: (data: any) => void;
  subject: any;
  name: string;
  onClick?: () => void;
}

const SubjectForm: React.FC<Props> = ({ onSubmit, subject, name, onClick }) => {
  const validationSchema: any = Yup.object({
    subjectname: Yup.string()
      .min(5)
      .max(30)
      .required("subjectname is required"),
  });
  const InputField = ({ field, form, ...props }: any) => {
    return <TextField {...props} {...field} />;
  };
  const addValues = {
    subjectname: undefined,
  };

  const editValues = subject && {
    subjectname: subject.subjectname,
  };
  const values = name === "edit" ? editValues : addValues;

  const classes = useStyles();

  return (
    <Container style={{ width: "100%", marginBottom: 20 }}>
      <Formik
        onSubmit={(data, { resetForm }) => {
          onSubmit({ row: data, id: subject.subjectId, subjectData: subject });
          if (name !== "edit") resetForm();
        }}
        initialValues={values}
        validationSchema={validationSchema}
      >
        {({ errors, touched, isValid }: FormikProps<any>) => (
          <Form className={classes.root}>
            <Box display="flex" p={1}>
              <Box p={1} flexGrow={1}>
                <div>
                  <Field
                    variant="outlined"
                    name="subjectname"
                    label="Subject Name"
                    helperText={
                      errors.subjectname && touched.subjectname
                        ? errors.subjectname
                        : null
                    }
                    error={touched.subjectname && Boolean(errors.subjectname)}
                    component={InputField}
                  />
                </div>
              </Box>
              <Box p={1}>
                <Button
                  variant="contained"
                  onClick={onClick}
                  className={classes.color}
                  type="submit"
                  disabled={!isValid}
                >
                  Submit
                </Button>
              </Box>
            </Box>
          </Form>
        )}
      </Formik>
    </Container>
  );
};

export default SubjectForm;
