import Box from "@material-ui/core/Box";
import { blue, pink } from "@material-ui/core/colors";
import Container from "@material-ui/core/Container";
import Paper from "@material-ui/core/Paper";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import Pagination from "@material-ui/lab/Pagination";
import TableRow from "@material-ui/core/TableRow";
import React, { Fragment, useState, useEffect } from "react";
import Grid from "@material-ui/core/Grid";
import {
  getExams,
  postExam,
  deleteExam,
  updateExam,
} from "../../services/examServices";
import ExamForm from "../Forms/examForm";
import Progress from "../Loading/progress";
import ExamPopForm from "../Forms/PopUpForms/examPop";
import SnackPar from "../Common/snackpar";
import Search from "../Search/search";
import DeletePopUp from "../Forms/PopUpForms/deletePop";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "",
  },
  table: {
    minWidth: 100,
  },
  gridRoot: {
    flexGrow: 1,
  },
  pagination: {
    margin: theme.spacing(2),
    marginTop: theme.spacing(5),
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "left",
    color: theme.palette.text.secondary,
  },
  button: {
    cursor: `pointer`,
  },
  head: {
    backgroundColor: `#f5f5f5`,
    color: theme.palette.common.white,
    fontStyle: "fontWeightBold",
  },

  fab: {
    margin: theme.spacing(2),
    color: theme.palette.getContrastText(pink[500]),
    backgroundColor: blue[600],
  },
}));

const isSearched = (value: string) => ({ examCode }: any) =>
  examCode.toLowerCase().includes(value.toLowerCase());

export function ExamTable() {
  const classes = useStyles();
  const [exams, setExams] = useState<Array<{}>>([]);
  const [isLoading, setLoading] = useState(true);
  const [errors, setErrors] = useState(undefined);
  const [value, setValue] = useState<string>("");
  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState("");
  const [totalPages, setTotalPages] = useState(0);
  const [page, setPage] = useState(1);
  const [totalItems, setTotalItems] = useState(0);
  const [size, setSize] = useState(5);

  useEffect(() => {
    const fetchExams = async (page: number, size: number) => {
      const { data } = await getExams(page, size);
      setTotalItems(data.totalItems);
      setTotalPages(data.totalPages);
      setExams(data.exams);
      setLoading(false);
    };
    fetchExams(page - 1, size);
  }, [page, size]);

  const handleSubmit = async ({ row }: any) => {
    try {
      const { data } = await postExam(row);
      const newExams = [...exams, data];
      setExams(newExams);
      handleClick("Sucessfully posted!");
    } catch (err) {
      handleClick("Sucessfully posted!");
      setErrors(err.message);
    }
  };

  const handleDelete = async (id: number) => {
    try {
      const cloneExams = [...exams];
      const newExams = cloneExams.filter((exam: any) => exam.examId !== id);
      setExams(newExams);
      await deleteExam(id);
      handleClick("Sucessfully deleted!");
    } catch (err) {
      handleClick("Sucessfully deleted!");
      setErrors(err.message);
      const cloneExams = [...exams];
      setExams(cloneExams);
    }
  };
  const fetchPaginatedData = (e: any, page: number) => {
    setPage(page);
  };

  const handleUpdate = async ({ row, id, examData }: any) => {
    try {
      const { data } = await updateExam({ row, id });
      const cloneExams = [...exams];
      const index = cloneExams.indexOf(examData);
      cloneExams[index] = { ...data };
      setExams(cloneExams);
      handleClick("Sucessfully updated!");
    } catch (err) {
      handleClick("Sucessfully updated!");
      setErrors(err.message);
    }
  };

  const handleClick = (msg: string) => {
    setMessage(msg);
    setOpen(true);
  };

  const handleClose = (event: any, reason: any) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  const handleChange = (e: any) => {
    if (e.target.value) {
      setValue(e.target.value);
      setSize(totalItems);
    } else {
      setValue(e.target.value);
      setSize(5);
    }
  };

  return (
    <Fragment>
      <SnackPar
        errors={errors}
        handleClose={handleClose}
        success={message}
        open={open}
      />
      <Container style={{ width: "100%", marginBottom: 20 }}>
        <Box display="flex" p={1}>
          <Box p={1} flexGrow={1}>
            <Search value={value} onChange={handleChange} by="Exam Code" />
          </Box>
        </Box>
      </Container>

      <div className={classes.gridRoot}>
        <Grid container spacing={3}>
          <Grid item xs={5}>
            {isLoading ? (
              <Progress />
            ) : (
              <TableContainer component={Paper} className={classes.root}>
                <Table className={classes.table} aria-label="simple table">
                  <TableHead className={classes.head}>
                    <TableRow>
                      <TableCell>#ID</TableCell>
                      <TableCell>Exam Code</TableCell>
                      <TableCell align="center">Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  {exams && (
                    <TableBody>
                      {exams.filter(isSearched(value)).map((exam: any) => (
                        <TableRow key={exam.examId}>
                          <TableCell component="th" scope="row">
                            {exam.examId}
                          </TableCell>
                          <TableCell>{exam.examCode}</TableCell>
                          <TableCell style={{ display: "flex" }}>
                            <ExamPopForm onSubmit={handleUpdate} exam={exam} />

                            <DeletePopUp
                              item={exam.examId}
                              onDelete={handleDelete}
                            />
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  )}
                </Table>
              </TableContainer>
            )}
            {totalPages !== 0 ? (
              <Pagination
                className={classes.pagination}
                count={totalPages}
                page={page}
                onChange={fetchPaginatedData}
                color="primary"
              />
            ) : null}
          </Grid>
          <Grid item xs={7}>
            <Paper elevation={0} variant="outlined" className={classes.paper}>
              <h3>Exam Form</h3>
              <ExamForm exam="exam" name="add" onSubmit={handleSubmit} />
            </Paper>
          </Grid>
        </Grid>
      </div>
    </Fragment>
  );
}
