import Container from "@material-ui/core/Container";
import CssBaseline from "@material-ui/core/CssBaseline";
import { createStyles, makeStyles } from "@material-ui/core/styles";
import React, { useState } from "react";
import ProtectedRoute from "../Common/protectedRoute";
import { Route, Switch, Redirect } from "react-router-dom";
import AppBar from "../AppBar";
import Home from "../Home/index";
import Levels from "../Levels";
import { Drawer } from "../SideBar";
import Students from "../Students";
import Subjects from "../Subjects";
import Teachers from "../Teachers";
import Profile from "../Common/Profile";
import Grades from "../Grades";
import Exams from "../Exams";
import NotFound from "../Common/NotFound";
import StudentView from "../Reports/studentReportView";
import TeacherView from "../Reports/teacherReport";
import AllStudentView from "../Reports/allStudentReport";
import AllTeacherView from "../Reports/allTeacherReport";

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      display: "flex",
    },
    button: {
      marginTop: 30,
    },
    toolbar: {
      paddingRight: 24, // keep right padding when drawer closed
    },

    appBarSpacer: theme.mixins.toolbar,
    content: {
      flexGrow: 1,
      height: "100vh",
      overflow: "auto",
    },
    container: {
      paddingTop: theme.spacing(4),
      paddingBottom: theme.spacing(4),
    },
  })
);

const Dashboard = () => {
  const [open, setOpen] = useState(true);
  const handleDrawerOpen = () => {
    setOpen(!open);
  };

  const handleDrawerClose = () => {
    setOpen(!open);
  };
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <CssBaseline />

      <AppBar open={open} onHandleDrawerOpen={handleDrawerOpen} />

      <Drawer open={open} onHandleDrawerClose={handleDrawerClose} />

      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          <Switch>
            <ProtectedRoute path="/dashboard/students" component={Students} />
            <ProtectedRoute path="/dashboard/teachers" component={Teachers} />
            <ProtectedRoute path="/dashboard/subjects" component={Subjects} />
            <ProtectedRoute path="/dashboard/levels" component={Levels} />
            <ProtectedRoute path="/dashboard/profile" component={Profile} />
            <Route path="/dashboard/not-found" component={NotFound} />
            <ProtectedRoute path="/dashboard/exams" component={Exams} />
            <ProtectedRoute
              path="/dashboard/studentview"
              component={StudentView}
            />
            <ProtectedRoute
              path="/dashboard/teacherview"
              component={TeacherView}
            />
            <ProtectedRoute
              path="/dashboard/allteacherview"
              component={AllTeacherView}
            />
            <ProtectedRoute
              path="/dashboard/allstudentsview"
              component={AllStudentView}
            />
            <ProtectedRoute path="/dashboard/grades" component={Grades} />
            <ProtectedRoute path="/dashboard" component={Home} />
            <Redirect to="/dashboard/not-found" />
          </Switch>
        </Container>
      </main>
    </div>
  );
};

export default Dashboard;
