import http from "./httpServices";

const URL = "/exams";

export const getExams = (page = 0, size = 5) => {
	const exams = http.get(`${URL}/?page=${page}&size=${size}`);
	return exams;
};

export const postExam = (data: any) => {
	const exam = http.post(URL, data);
	return exam;
};

export const deleteExam = (id: number) => {
	const exam = http.delete(`${URL}/${id}`);
	return exam;
};

export const updateExam = (data: any) => {
	const exam = http.put(`${URL}/${data.id}`, data.row);
	return exam;
};
