import Box from "@material-ui/core/Box";
import { blue, pink } from "@material-ui/core/colors";
import Container from "@material-ui/core/Container";
import IconButton from "@material-ui/core/IconButton";
import Paper from "@material-ui/core/Paper";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import Avatar from "@material-ui/core/Avatar";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import VisibilityIcon from "@material-ui/icons/Visibility";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Tooltip from "@material-ui/core/Tooltip";
import Pagination from "@material-ui/lab/Pagination";
import React, { Fragment, useEffect, useState } from "react";
import Person from "@material-ui/icons/Person";
import TeacherForm from "../Forms/teacherForm";
import {
	deleteTeacher,
	getTeachers,
	postTeachers,
	updateTeacher,
} from "../../services/teacherServices";
import Progress from "../Loading/progress";
import Search from "../Search/search";
import SnackPar from "../Common/snackpar";
import FilterSize from "../Common/filter";
import DeletePopUp from "../Forms/PopUpForms/deletePop";
import { Link } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
	root: {
		"& > *": {
			borderBottom: "unset",
		},
	},
	blueLight: {
		color: blue[600],
	},
	table: {
		minWidth: 650,
	},
	button: {
		cursor: `pointer`,
	},
	head: {
		backgroundColor: `#f5f5f5`,
		color: theme.palette.common.white,
		fontStyle: "fontWeightBold",
	},
	avatar: {
		color: theme.palette.getContrastText(pink[500]),
		backgroundColor: blue[600],
	},
	pagination: {
		margin: theme.spacing(2),
		marginTop: theme.spacing(5),
	},

	report: {
		margin: theme.spacing(1),
		color: blue[600],
		cursor: `pointer`,
	},
	editButton: {
		color: blue[600],
		cursor: `pointer`,
	},
	fab: {
		margin: theme.spacing(2),
		color: theme.palette.getContrastText(pink[500]),
		backgroundColor: blue[600],
	},
}));

const isSearched = (value: string) => ({ firstname }: any) =>
	firstname.toLowerCase().includes(value.toLowerCase());

export default function TeacherTable() {
	const classes = useStyles();
	const [teachers, setTeacherDatas] = useState<Array<{}>>([]);
	const [isLoading, setLoading] = useState(true);
	const [value, setValue] = useState<string>("");
	const [open, setOpen] = useState(false);
	const [errors, setErrors] = useState(null);
	const [message, setMessage] = useState("");
	const [totalPages, setTotalPages] = useState(0);
	const [page, setPage] = useState(1);
	const [totalItems, setTotalItems] = useState(0);
	const [size, setSize] = useState(10);
	const handleSubmit = async ({ row }: any) => {
		try {
			const { data } = await postTeachers(row);
			const newArray = [...teachers, data];
			setTeacherDatas(newArray);
			handleClick("Sucessfully posted!");
		} catch (err) {
			handleClick("Sucessfully posted!");
			setErrors(err.message);
		}
	};

	const handleUpdate = async ({ row, id, dataIndex }: any) => {
		try {
			const { data } = await updateTeacher({ row, id });
			const state = [...teachers];
			const index = state.indexOf(dataIndex);
			state[index] = { ...data };
			setTeacherDatas(state);
			handleClick("Sucessfully updated!");
		} catch (err) {
			handleClick("Sucessfully updated!");
			setErrors(err.message);
		}
	};

	const handleDelete = async (id: number) => {
		try {
			const newArray = teachers.filter(
				(teacher: any) => teacher.teacherId !== id
			);
			setTeacherDatas(newArray);
			await deleteTeacher(id);
			handleClick("Sucessfully deleted!");
		} catch (err) {
			handleClick("Sucessfully deleted!");
			setErrors(err.message);
			const cloneTeachers = [...teachers];
			setTeacherDatas(cloneTeachers);
		}
	};

	useEffect(() => {
		const feachTeachers = async (page: number, size: number) => {
			const { data } = await getTeachers(page, size);
			setTotalItems(data.totalItems);
			setTotalPages(data.totalPages);
			setTeacherDatas(data.teachers);
			setLoading(false);
		};

		feachTeachers(page - 1, size);
	}, [page, size]);

	const handleChange = (e: any) => {
		if (e.target.value) {
			setValue(e.target.value);
			setSize(totalItems);
		} else {
			setValue(e.target.value);
			setSize(10);
		}
	};
	const fetchPaginatedData = (e: any, page: number) => {
		setPage(page);
	};

	const handleSize = (size: number) => {
		setSize(size);
		setPage(1);
	};

	const handleClick = (msg: string) => {
		setMessage(msg);
		setOpen(true);
	};

	const handleClose = (event: any, reason: any) => {
		if (reason === "clickaway") {
			return;
		}

		setOpen(false);
	};

	return (
		<Fragment>
			<SnackPar
				errors={errors}
				handleClose={handleClose}
				success={message}
				open={open}
			/>
			<Container style={{ width: "100%", marginBottom: 20 }}>
				<Box display="flex" p={1}>
					<Box p={1} flexGrow={1}>
						<Search
							value={value}
							onChange={handleChange}
							by="First Name"
						/>
					</Box>
					<Box p={2}>
						<FilterSize
							onSize={handleSize}
							totalItems={totalItems}
						/>
					</Box>
					<Box p={3}>
						<Tooltip title="Create Report">
							<IconButton
								className={classes.report}
								aria-label="report"
							>
								<Link
									style={{ height: "34px" }}
									className={classes.blueLight}
									to={{
										pathname: "/dashboard/allteacherview",
										state: {
											teachers,
										},
									}}
								>
									<VisibilityIcon fontSize="large" />
								</Link>
							</IconButton>
						</Tooltip>
					</Box>
					<Box p={1}>
						<Tooltip title="Add" aria-label="add">
							<TeacherForm
								onSubmit={handleSubmit}
								name="add"
								teacher={teachers}
							/>
						</Tooltip>
					</Box>
				</Box>
			</Container>
			{isLoading ? (
				<Progress />
			) : (
				<TableContainer component={Paper}>
					<Table className={classes.table} aria-label="simple table">
						<TableHead className={classes.head}>
							<TableRow>
								<TableCell></TableCell>
								<TableCell>ID</TableCell>
								<TableCell>First Name</TableCell>
								<TableCell>Second Name</TableCell>
								<TableCell>Last Name</TableCell>
								<TableCell>Contact Number</TableCell>
								<TableCell
									align="left"
									style={{ paddingLeft: 40 }}
								>
									Actions
								</TableCell>
							</TableRow>
						</TableHead>
						{teachers && (
							<TableBody>
								{teachers
									.filter(isSearched(value))
									.map((teacher: any) => (
										<TableRow key={teacher.id}>
											<TableCell>
												<Avatar
													aria-label="recipe"
													className={classes.avatar}
												>
													<Person />
												</Avatar>
											</TableCell>
											<TableCell
												component="th"
												scope="row"
											>
												{teacher.teacherId}
											</TableCell>
											<TableCell>
												{teacher.firstname}
											</TableCell>
											<TableCell>
												{teacher.secondname}
											</TableCell>
											<TableCell>
												{teacher.lastname}
											</TableCell>
											<TableCell>
												{teacher.contact}
											</TableCell>

											<TableCell
												style={{ display: "flex" }}
											>
												<TeacherForm
													onSubmit={handleUpdate}
													name="edit"
													teacher={teacher}
												/>

												<DeletePopUp
													item={teacher.teacherId}
													onDelete={handleDelete}
												/>
												<Tooltip title="Create Report">
													<IconButton
														className={
															classes.blueLight
														}
														aria-label="report"
													>
														<Link
															style={{
																height: "24px",
															}}
															className={
																classes.blueLight
															}
															to={{
																pathname:
																	"/dashboard/teacherview",
																state: {
																	teacher,
																},
															}}
														>
															<VisibilityIcon />
														</Link>{" "}
													</IconButton>
												</Tooltip>
											</TableCell>
										</TableRow>
									))}
							</TableBody>
						)}
					</Table>
				</TableContainer>
			)}
			{totalPages !== 0 ? (
				<Pagination
					className={classes.pagination}
					count={totalPages}
					page={page}
					onChange={fetchPaginatedData}
					color="primary"
				/>
			) : null}
		</Fragment>
	);
}
