import http from "./httpServices";

const URL = "/levels";

export const getLevels = (page = 0, size = 5) => {
	const levels = http.get(`${URL}/?page=${page}&size=${size}`);

	return levels;
};

export const postLevels = (data: any) => {
	const level = http.post(URL, data);
	return level;
};

export const deleteLevel = (id: number) => {
	const level = http.delete(`${URL}/${id}`);
	return level;
};

export const updateLevel = (data: any) => {
	const level = http.put(`${URL}/${data.id}`, data.row);
	return level;
};
