import Box from "@material-ui/core/Box";
import { blue, pink } from "@material-ui/core/colors";
import Container from "@material-ui/core/Container";
import Paper from "@material-ui/core/Paper";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Tooltip from "@material-ui/core/Tooltip";
import React, { Fragment, useEffect, useState } from "react";
import Grid from "@material-ui/core/Grid";
import SnackPar from "../Common/snackpar";
import Pagination from "@material-ui/lab/Pagination";

import {
  deletesubject,
  getSubjects,
  postSubjects,
  updatesubject,
} from "../../services/subjectService";
import SubjectForm from "../Forms/subjectForm";
import Progress from "../Loading/progress";
import SubjectPopForm from "../Forms/PopUpForms/SubjectPop";
import Search from "../Search/search";
import DeletePopUp from "../Forms/PopUpForms/deletePop";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "",
  },
  table: {
    minWidth: 100,
  },
  gridRoot: {
    flexGrow: 1,
  },

  paper: {
    padding: theme.spacing(2),
    textAlign: "left",
    color: theme.palette.text.secondary,
  },
  button: {
    cursor: `pointer`,
  },
  head: {
    backgroundColor: `#f5f5f5`,
    color: theme.palette.common.white,
    fontStyle: "fontWeightBold",
  },

  editButton: {
    color: blue[600],
    cursor: `pointer`,
  },
  pagination: {
    margin: theme.spacing(2),
    marginTop: theme.spacing(5),
  },
  fab: {
    margin: theme.spacing(2),
    color: theme.palette.getContrastText(pink[500]),
    backgroundColor: blue[600],
  },
}));

const isSearched = (value: string) => ({ subjectname }: any) =>
  subjectname.toLowerCase().includes(value.toLowerCase());

export function SubjectTable() {
  const classes = useStyles();
  const [subjects, setSubjects] = useState<Array<{}>>([]);
  const [isLoading, setLoading] = useState(true);
  const [value, setValue] = useState<string>("");
  const [errors, setErrors] = useState(null);
  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState("");
  const [totalItems, setTotalItems] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const [page, setPage] = useState(1);
  const [size, setSize] = useState(5);

  useEffect(() => {
    const fetchSubjects = async (page: number, size: number) => {
      const { data } = await getSubjects(page, size);
      setTotalItems(data.totalItems);
      setTotalPages(data.totalPages);
      setSubjects(data.subjects);
      setLoading(false);
    };
    fetchSubjects(page - 1, size);
  }, [page, size]);

  const handleSubmit = async ({ row }: any) => {
    try {
      const { data } = await postSubjects(row);
      const newsubjects = [...subjects, data];
      setSubjects(newsubjects);
      handleClick("Sucessfully posted!");
    } catch (err) {
      handleClick("Sucessfully posted!");
      setErrors(err.message);
    }
  };

  const handleDelete = async (id: number) => {
    try {
      const clonesubjects = [...subjects];
      const newsubjects = clonesubjects.filter(
        (subject: any) => subject.subjectId !== id
      );

      setSubjects(newsubjects);
      await deletesubject(id);
      handleClick("Sucessfully deleted!");
    } catch (err) {
      handleClick("Sucessfully deleted!");

      setErrors(err.message);
      const cloneSubjects = [...subjects];
      setSubjects(cloneSubjects);
    }
  };

  const handleUpdate = async ({ row, id, subjectData }: any) => {
    try {
      const { data } = await updatesubject({ row, id });
      const clonesubjects = [...subjects];
      const index = clonesubjects.indexOf(subjectData);
      clonesubjects[index] = { ...data };
      setSubjects(clonesubjects);
      handleClick("Sucessfully updated!");
    } catch (err) {
      handleClick("Sucessfully updated!");

      setErrors(err.message);
    }
  };

  const fetchPaginatedData = (e: any, page: number) => {
    setPage(page);
  };
  const handleClick = (msg: string) => {
    setMessage(msg);
    setOpen(true);
  };

  const handleClose = (event: any, reason: any) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const handleChange = (e: any) => {
    if (e.target.value) {
      setValue(e.target.value);
      setSize(totalItems);
    } else {
      setValue(e.target.value);
      setSize(5);
    }
  };
  return (
    <Fragment>
      <SnackPar
        errors={errors}
        handleClose={handleClose}
        success={message}
        open={open}
      />
      <Container style={{ width: "100%", marginBottom: 20 }}>
        <Box display="flex" p={1}>
          <Box p={1} flexGrow={1}>
            <Search value={value} onChange={handleChange} by="Subject Name" />
          </Box>
        </Box>
      </Container>

      <div className={classes.gridRoot}>
        <Grid container spacing={3}>
          <Grid item xs={5}>
            {isLoading ? (
              <Progress />
            ) : (
              <TableContainer component={Paper} className={classes.root}>
                <Table className={classes.table} aria-label="simple table">
                  <TableHead className={classes.head}>
                    <TableRow>
                      <TableCell>#ID</TableCell>
                      <TableCell>Subject Name</TableCell>
                      <TableCell align="center">Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  {subjects && (
                    <TableBody>
                      {subjects
                        .filter(isSearched(value))
                        .map((subject: any) => (
                          <TableRow key={subject.subjectId}>
                            <TableCell component="th" scope="row">
                              {subject.subjectId}
                            </TableCell>
                            <TableCell>{subject.subjectname}</TableCell>
                            <TableCell style={{ display: "flex" }}>
                              <Tooltip title="Edit">
                                <SubjectPopForm
                                  onSubmit={handleUpdate}
                                  subject={subject}
                                />
                              </Tooltip>

                              <DeletePopUp
                                item={subject.subjectId}
                                onDelete={handleDelete}
                              />
                            </TableCell>
                          </TableRow>
                        ))}
                    </TableBody>
                  )}
                </Table>
              </TableContainer>
            )}
            {totalPages !== 0 ? (
              <Pagination
                className={classes.pagination}
                count={totalPages}
                page={page}
                onChange={fetchPaginatedData}
                color="primary"
              />
            ) : null}
          </Grid>
          <Grid item xs={7}>
            <Paper elevation={0} variant="outlined" className={classes.paper}>
              <h3>Subject Form</h3>
              <SubjectForm
                subject="subject"
                name="add"
                onSubmit={handleSubmit}
              />
            </Paper>
          </Grid>
        </Grid>
      </div>
    </Fragment>
  );
}
