import React from "react";
import { Field, Form, Formik, FormikProps } from "formik";
import * as Yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import { blue, pink } from "@material-ui/core/colors";
import Container from "@material-ui/core/Container";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "100%",
      height: "10ch",
    },
  },
  color: {
    backgroundColor: blue[600],
    color: theme.palette.getContrastText(pink[500]),
  },
}));

interface Props {
  onSubmit: (data: any) => void;
  exam: any;
  name: string;
  onClick?: () => void;
}

const ExamForm: React.FC<Props> = ({ onSubmit, exam, name, onClick }) => {
  const validationSchema: any = Yup.object({
    examCode: Yup.string().min(3).max(20).required("examCode is required"),
  });
  const InputField = ({ field, form, ...props }: any) => {
    return <TextField {...props} {...field} />;
  };
  const addValues = {
    examCode: undefined,
  };

  const editValues = exam && {
    examCode: exam.examCode,
  };
  const values = name === "edit" ? editValues : addValues;
  const classes = useStyles();
  return (
    <Container>
      <Formik
        onSubmit={(data, { resetForm }) => {
          onSubmit({ row: data, id: exam.examId, examData: exam });
          if (name !== "edit") resetForm();
        }}
        initialValues={values}
        validationSchema={validationSchema}
      >
        {({ errors, touched, isValid }: FormikProps<any>) => (
          <Form className={classes.root}>
            <Box display="flex" p={1}>
              <Box p={1} flexGrow={1}>
                <div>
                  <Field
                    variant="outlined"
                    name="examCode"
                    label="Exam Code"
                    helperText={
                      errors.examCode && touched.examCode
                        ? errors.examCode
                        : null
                    }
                    error={touched.examCode && Boolean(errors.examCode)}
                    component={InputField}
                  />
                </div>
              </Box>
              <Box p={1}>
                <Button
                  variant="contained"
                  onClick={onClick}
                  className={classes.color}
                  type="submit"
                  disabled={!isValid}
                >
                  Submit
                </Button>
              </Box>
            </Box>
          </Form>
        )}
      </Formik>
    </Container>
  );
};

export default ExamForm;
