import React from "react";
import {
	BarChart,
	Bar,
	XAxis,
	YAxis,
	CartesianGrid,
	Tooltip,
	Legend,
} from "recharts";
import _ from "lodash";

interface Props {
	students: any;
}

const LevelGraph = ({ students }: Props) => {
	const ids: number[] = [];
	students.forEach((student: any) => {
		const levelIds = [student.levelId];
		ids.push(...levelIds);
	});

	const data = _.values(_.groupBy(ids)).map((d) => ({
		levelId: `level ${d[0]}`,
		students: d.length,
	}));
	return (
		<BarChart
			width={690}
			height={300}
			data={data}
			margin={{
				top: 5,
				right: 30,
				left: 20,
				bottom: 5,
			}}
			barSize={20}
		>
			<XAxis
				dataKey="levelId"
				scale="point"
				padding={{ left: 10, right: 10 }}
			/>
			<YAxis />
			<Tooltip />
			<Legend />
			<CartesianGrid strokeDasharray="3 3" />
			<Bar
				dataKey="students"
				fill="#1e88e5"
				background={{ fill: "#ccc" }}
			/>
		</BarChart>
	);
};

export default LevelGraph;
