import http from "./httpServices";

const URL = "/students";

export const getStudents = (page = 0, size = 10) => {
	const students = http.get(`${URL}/?page=${page}&size=${size}`);
	return students;
};

export const postStudents = (data: any) => {
	const student = http.post(URL, data);
	return student;
};

export const deleteStudent = (id: number) => {
	const student = http.delete(`${URL}/${id}`);
	return student;
};

export const updateStudent = (data: any) => {
	const student = http.put(`${URL}/${data.id}`, data.row);
	return student;
};
